.wrapper {
    display: flex;
    padding: 9px 16px;
    cursor: pointer;
    &:hover {
        background-color: rgba(22, 24, 35, 0.03);
    }
}
.avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}
.info {
    margin-left: 16px;
    line-height: 1;
}
.name {
    font-size: 1.6rem;
    font-weight: 600;
    color: #0a0c11;
}
.userName {
    font-size: 1.4rem;
    color: rgba(22, 24, 35, 0.5);
}
.check {
    margin-left: 6px;
    font-size: 1.4rem;
    color: rgb(32, 213, 236);
}
