.wrapper {
    font-size: 1.6rem;
    font-weight: 700;
    min-width: 100px;
    padding: 9px 16px;
    border-radius: 4px;
    cursor: pointer;
    border: 1px solid transparent;
    user-select: none;
    // 2 nút đứng cạnh nhau liền kề
    + .wrapper {
        margin-left: 8px;
        text-align: center;
    }
    &.disabled {
        pointer-events: none;
        opacity: 0.5;
    }
    .menu-item {
        background-color: transparent;
    }
}
.icon + .title,
.title + .icon {
    margin-left: 8px;
}
.icon {
    display: inline-block;
    width: 20px;
}
.rounded {
    border-radius: 999px;
    border: 1px solid rgba(22, 24, 35, 0.12);

    box-shadow: rgba(0, 0, 0, 0.06) 0px 2px 8px;
    &:hover {
        border-color: rgba(22, 24, 35, 0.2);
        background-color: rgb(255, 255, 255);
    }
}
.primary {
    background-color: var(--primary);
    color: #fff;
    border-color: var(--primary);

    &:hover {
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.06), rgba(0, 0, 0, 0.06)), rgb(254, 44, 85);
    }
}
.outLine {
    color: var(--primary);
    border-color: currentColor;
    &:hover {
        background-color: rgba(254, 44, 85, 0.06);
        border-color: var(--primary);
    }
}
.small {
    min-width: 88px;
    padding: 4px 16px;
}
.large {
    padding: 14px 16px;
    min-width: 148px;
}
.text {
    background-color: #fff;
    &:hover {
        text-decoration: underline;
    }
}
